<template>
  <v-ons-page>
    <!--<v-ons-toolbar class="bg-white" modifier="cover-content transparent noshadow"></v-ons-toolbar>-->

    <div class="header bg-white">
        <img class="ppicture" src="/static/assets/profileimage.jpeg" alt="Person Name">
        <h4 class="user">Heri Agape</h4>
        <a href="#" class="small-link">See Profile</a>
      <div class="rating">
        <span><v-ons-icon icon="fa-star"></v-ons-icon> 3.5</span>
      </div>
    </div>

    <!-- <v-ons-list-title class="pl-40">Little Menu</v-ons-list-title> -->
    <v-ons-list class="menu" style="padding-top: 24px;">
      <v-ons-list-item class="" modifier="nodivider" v-for="item in essentialLinks" @click="goTo(item.link)" :key="item.link" tappable>
        <div class="left">
          <v-ons-icon :icon="item.icon" class="list-item__icon"></v-ons-icon>
        </div>
        <div class="center">{{ item.label }}</div>
      </v-ons-list-item>
      <v-ons-list-item class="">
        <div class="center">
          Panic Button
        </div>
        <div class="right">
          <v-ons-switch></v-ons-switch>
        </div>
      </v-ons-list-item>
    </v-ons-list>

    <div class="page-footer">
      <v-ons-list class="menu">
        <v-ons-list-item modifier="nodivider">

          <div class="left">
            <a class="small-link" href="#">Settings</a>
          </div>
          <div class="center text-center" style="padding-left: 20px">
            <a class="small-link" href="#">Help</a>
          </div>
          <div class="right ">
            <a class="small-link" href="#"><i>v2.53</i></a>
          </div>
        </v-ons-list-item>
      </v-ons-list>
    </div>
  </v-ons-page>
</template>

<script>
export default {
  name: 'menu',
  data () {
    return {
      essentialLinks: [
        {
          label: 'My Rides',
          link: 'rides',
          icon: 'fa-car'
        },
        {
          label: 'Payments',
          link: 'payement',
          icon: 'fa-cc-visa'
        },
        {
          label: 'Little Wallet',
          link: 'wallet',
          icon: 'fa-credit-card'
        },
        {
          label: 'Corporate',
          link: 'corp',
          icon: 'fa-building'
        },
        {
          label: 'Promotions',
          link: 'promo',
          icon: 'ion-pricetags',
          children: [
            {
              label: 'Free Rides',
              link: 'free',
              icon: 'fa-gratipay'
            },
            {
              label: 'Gift a Promo',
              link: 'gift',
              icon: 'fa-gift'
            },
          ]
        }
      ]
    }
  },
  methods: {
    goTo (url) {
      console.log("clicked", url)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header {
  position: relative;
  text-align: left;
  padding: 20px;
  padding-left: 40px;
  padding-top: 50px;
}
.user{
  font-size: 16px;
}

.menu{
  background: #efeff4;
  font-size: 15px;
}

.pl-40{
  padding-left: 40px;
  padding-right: 25px;
}

.text-center{
  text-align: center;
}

.page-footer{
  bottom: 0;
  left: 0;
  right: 0;
  position: absolute;
}

.bg-white{
  background-color: white;
}

.small-link{
  font-size: 12px;
  color: #848484;
  text-decoration: none;
}

.rating {
  position: absolute;
  top: 50px;
  right: 50px;
  font-size: 12px;
  color: #ffac4c;
}


.ppicture{
  height: 60px;
  width: 60px;
  border-radius: 50%;
}

ons-list-title {
  text-transform: none;
}

ons-list-item {
  color: rgba(0, 0, 0, 0.54) !important;
  cursor: pointer;
}

.list-item__icon {
    font-size: 15px;
    padding: 0 6px;
}
</style>



