
import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import {EventBus} from '../utils/bus'

Vue.use(Vuex);
export default new Vuex.Store({
  // strict: true,
  modules: {
    splitter: {
      namespaced: true,
      state: {
        open: false
      },
      mutations:{
        toggle (state, shouldOpen) {
          if (typeof shouldOpen === 'boolean') {
            state.open = shouldOpen
          } else {
            state.open = !state.open
          }
        },
      },
      actions: {
        toggleMenu: function (context) {
          context.commit('toggle');
        },
      }
    },
    user: {
      namespaced: true,
      state: {
        netAddress: {},
      },
      mutations:{
        userIPlocation (state, location_on_ip) {
          Object.assign(state.netAddress, location_on_ip);
        },
      },
      actions: {
        ipLocation: async function (context) {
          try {
            const response = await axios.get('https://ipinfo.io');
            context.commit('userIPlocation', response.data);
            EventBus.$emit('countryCode', response.data.country.toLowerCase())
          } catch (error) {
            EventBus.$ons.notification.toast(error.message);
            console.info("Location by IP faild")
            // console.error(error);
          }
        },
      },
      getters:{
        getIPLocation: function(state) {
          return state.netAddress;
        }
      }
    },
    map: {
      namespaced: true,
      state: {
        MAP: {},
        DIRECTIONSERVICE: {},
        MYLOCATION: {},
        myLocationImage: {
          url: '/static/assets/location.png',
          size: new google.maps.Size(48, 48),
          origin: new google.maps.Point(0, 0),
          anchor: new google.maps.Point(24, 24)
        },
        originAddress: "",
        destinationAddress: ""
      },
      mutations: {
        updateMap: function (state, map) {
          Object.assign(state.MAP, map);
        },
        updateDirectionService: function (state, directionService) {
          Object.assign(state.DIRECTIONSERVICE, directionService);
        },
        updateMyLocation: function (state, mylocation) {
          Object.assign(state.MYLOCATION, mylocation);
        },
        updateOriginAddress: function (state, content) {
          state.originAddress = content;
        },
        updateDestinationAddress: function (state, content) {
          state.destinationAddress = content
        }
      },
      actions: {
        setMap: function (context, map) {
          context.commit('updateMap', map);
        },
        setDirectionSerive: function (context, directionService) {
          context.commit('updateDirectionService', directionService);
        },
        setMyLocation: function (context, myLocation) {
          context.commit('updateMyLocation', myLocation);
        }
      },
      getters:{
        getMeta: function(state) {
          return state.DIRECTIONSERVICE.meta
        }
      }
    }
  }
})



