// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

// import 'font-awesome/css/font-awesome.css'
import 'onsenui/css/font_awesome/css/font-awesome.min.css';
import 'onsenui/css/ionicons/css/ionicons.min.css';
import 'onsenui/css/material-design-iconic-font/css/material-design-iconic-font.min.css';

import 'onsenui/css/onsenui-core.css'
import 'onsenui/css/onsen-css-components.css'

import 'vue-swipe/dist/vue-swipe.css';


import Vue from 'vue'
import $ons from 'vue-onsenui/esm'
import * as VOns from './vue-onsen-components'
import store from './utils/store'
import App from './App'
import router from './utils/router';
import PhoneInput from 'vue2-phone-input';

$ons.platform.select('ios');

Vue.config.productionTip = false
Vue.component('phone-input', PhoneInput);

Vue.use($ons);

Object.values(VOns).forEach(comp => Vue.component(comp.name, comp))

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  template: '<App/>',
  components: { App }
})





